import React, {Component} from 'react'
import {Card, Tabs, Button, Form, Select, Input, Table} from 'antd'
import './index.scss'

const columns = [
  {title: '', width: 100, dataIndex: 'name', key: 'name', fixed: 'left'},
  {title: '', width: 100, dataIndex: 'age', key: 'age', fixed: 'left'},
  {title: '1', dataIndex: 'address', key: '1', width: 150},
  {title: '2', dataIndex: 'address', key: '2', width: 150},
  {title: '3', dataIndex: 'address', key: '3', width: 150},
  {title: '4', dataIndex: 'address', key: '4', width: 150},
  {title: '5', dataIndex: 'address', key: '5', width: 150},
  {title: '6', dataIndex: 'address', key: '6', width: 150},
  {title: '7', dataIndex: 'address', key: '7', width: 150},
  {title: '8', dataIndex: 'address', key: '8', width: 150},
  {
    title: '',
    key: 'operation',
    fixed: 'right',
    width: 100,
    render: () => <a href="javascript:;"></a>,
  },
];

const data = [];
for (let i = 0; i < 100; i++) {
  data.push({
    key: i,
    name: `${i}`,
    age: 32,
    address: `${i}`,
  });
}


//  rowSelection 
const rowSelection = {
  getCheckboxProps: record => ({
    disabled: record.name === '',    // 
  }),
};


class LoginUser extends Component {
  constructor() {
    super()

    this.state = {
      schoolList: ['1', '2', '3']
    }

    this.handleChange = this.handleChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
  }

  handleChange(val) {
    const schoolList = this.state.schoolList
    // fetch schoolList
    this.setState({
      schoolList: this.state.schoolList.filter(_ => _.includes(val))
    })
  }

  handleForgetPassword() {
  }

  handleSubmit() {
  }

  render() {
    return (
      <div class="align-center p-5">
        <img src={require('@/assets/student-logo.png')}/>

        <Table columns={columns} dataSource={data} scroll={{x: 1500, y: 300}}/>
      </div>
    )
  }
}

export default LoginUser



